:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.home-list {
  width: 300px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 0;
  margin-bottom: 20px;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.home-item {
  letter-spacing: 2px;
  color: #000;
  border-radius: 10px;
  margin: 10px 20px;
  font-family: Montserrat, sans-serif;
  text-decoration: none;
  transition: all .15s ease-in-out;
  display: flex;
  box-shadow: inset 0 0 40px 40px #000, 0 0 #fff;
}

.home-item:hover {
  color: #000;
  box-shadow: inset 0 0 10px #000, 0 0 10px 4px #000;
}

.link-back {
  color: #fff;
  padding: 15px 30px;
  text-decoration: none;
}

.link-back:hover {
  color: #000;
}

